body {
	background: $main-color-light;
	// +css-lock(16, 20, 600, 2000)
	line-height: 1.4;
	font-family: $Montserrat, sans-serif;
	overflow-x: hidden;
	@include smooth-scroll;
	@include smooth-font; }
* {
	box-sizing: border-box;
 }	// box-shadow: 0 0 0 1px #00f

image {
	max-width: 100%;
	height: auto; }

a {
	text-decoration: none;
	color: inherit; }

// +smooth-scroll

// ===== LAYOUT =====

.container-inner {
	max-width: 970px;
	margin: 0 auto;
	position: relative;
	@include md {
		width: auto;
		margin: 0 60px; }
	@include sm {
		margin: 0 15px; } }

.container {
	margin: 0 auto;
	padding: 0 15px;
	@include md {
		padding: 0; } }


.white-rounded-container--outer {
	margin: 0 auto;
	// width: 1170px
	max-width: 1170px;
	background-color: #fff;
	box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
	border-radius: 16px;
	@include md {
		margin: 0 36px; }
	@include sm {
		margin: 0 5px; } }

.white-rounded-container--inner {
	width: 970px;
	margin: 0 auto;
	padding: 88px 0;
	position: relative;
	@include md {
		width: auto;
		padding: 48px 36px; }
	@include sm {
		padding: 32px 10px; } }


// ===== BUTTONS =====
.btn {
	border-radius: 32px;
	height: 64px;
	margin: 0 auto;
	line-height: 64px;
	font-size: 18px;
	font-weight: 600;
	text-align: center; }

.btn-inline {
	display: inline-block;
	padding: 0 65px; }

.btn-block {
	display: block;
	width: 100%; }

.btn-dark {
	background-color: $main-color-dark;
	color: #fff; }

.btn-light {
	background-color: #fff;
	color: $main-color-dark; }

// ===== TITLES =====
.title--large {
	font-size: 60px;
	font-weight: 600;
	line-height: 1;
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 36px; } }

.title--xlarge {
	font-size: 72px;
	font-weight: 600;
	line-height: 1;
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 36px; } }

.title--thin {
	font-weight: 200; }
