.header {
    position: relative;
    padding: 66px 0 117px;
    z-index: 15;
    @include sm {
        padding: 24px 0 72px; } }

.top {
    position: relative;
    height: 100%;
    display: flex;
    align-items: center;
    z-index: 15;
    &__logo {
        flex-grow: 1;
        margin-left: 25px;
        @include sm {
            margin-left: 0; } }
    &__nav {
        margin-left: auto;
        @include sm {
            display: none; } }
    &__cta {
        margin-right: 9px;
        @include sm {
            display: none; } }
    &__lang-change {
        @include sm {
            display: none; } }
    &__toggler {
        display: none;
        @include sm {
            display: block;
            margin-left: auto; } } }

.logo {
    display: block;
    &__image {
       display: inline-block;
       margin-right: 12px;
       @include md {
            margin-right: 8px;
            width: 36px; } }


    &__text {
        display: inline-block;
        color: #fff;
        vertical-align: top;
        line-height: 0; }
    &__upper-text {
        font-size: 24px;
        font-weight: 600;
        line-height: 28px;
        @include md {
            font-size: 18px; }

        &--coloured {
            color: #56ecf6; } }
    &__bottom-text {
        font-size: 13px;
        font-weight: 500;
        @include md {
            font-size: 10px; } } }


.nav {
    display: flex;
    align-items: center;
    &__item {
        margin-right: 36px;
        @include md {
            margin-right: 20px; } }
    &__link {
        display: block;
        font-size: 16px;
        font-weight: 600;
        color: #fff;
        @include md {
            font-size: 14px; } } }

.cta {
    display: block;
    padding: 13px 30px;
    border-radius: 16px;
    font-size: 16px;
    font-weight: 600;
    color: $main-color-dark;
    background-color: #ffffff;
    @include md {
        font-size: 14px;
        padding: 13px 18px; } }

.lang-changer {
    display: block;
    position: relative;
    padding: 13px 30px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    border-radius: 16px 16px 0 0;
    text-align: center;
    @include md {
        padding: 13px 20px;
        font-size: 14px; }
    &__btn--inactive {
        display: none; }
    &__btn--active {
        display: block; }

    &--open {
        background-color: #ffffff;
        color: $main-color-dark; }
    &--open &__btn--inactive {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 100%;
            left: 0;
            right: 0;
            height: 100%;
            border-radius: 0 0 16px 16px;
            background-color: #ffffff; } }

.icon-toggler {
    fill: #fff;
    height: 24px;
    width: 24px;
    cursor: pointer; }


