.forecast {
    position: relative;
    background-color: $main-color-dark;
    z-index: 10;
    padding: 144px 0 144px;
    color: #fff;
    @include md {
        padding: 60px 0 80px; }
    @include sm {
        padding: 30px 0 50px; }
    // margin-top: -48px
    // overflow-y: hidden
    &__container {
        position: relative;
        z-index: 5; }
    &__descr {
        margin-bottom: 43px;
        font-size: 24px;
        font-weight: 600;
        line-height: 1.5; }
    &__buttons {
        display: flex;
        justify-content: space-between;
        margin-bottom: 42px;
        @include sm {
            flex-wrap: wrap; } }
    &__btn {
        display: block;
        width: 136px;
        height: 84px;
        border-radius: 8px;
        text-align: center;
        border: solid 8px #223e5c;
        line-height: 68px;
        font-size: 30px;
        font-weight: bold;
        vertical-align: top;
        text-transform: uppercase;
        &:not(:last-child) {
            margin-right: 30px;
            @include sm {
                margin-right: 0; } }
        &-active {
            background-color: $main-color-light;
            border: solid 8px $main-color-light; }
        @include md {
            height: 60px;
            border: solid 6px #223e5c;
            font-size: 18px;
            line-height: 48px; }
        @include sm {
            height: 48px;
            width: 30%;
            font-size: 16px;
            line-height: 36px; } }


    &__image {
        position: relative;
        z-index: 5;
        img {
            width: 100%; } }
    &:before {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 200%;
        background-color: $main-color-dark;
        z-index: 10;
        transform: skewY(30deg);
        transform-origin: top right;
        z-index: 2;
        @include md {
            height: 150%; }
        @include sm {
            height: 100%; } }
    &:after {
        position: absolute;
        content: '';
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 200%;
        background-color: $main-color-dark;
        z-index: 10;
        transform: skewY(30deg);
        transform-origin: bottom left;
        z-index: 2;
        @include md {
            height: 150%; }
        @include sm {
            height: 100%; } } }

.forecast {
    &__cube-top {
        position: absolute;
        top: -66vw;
        left: -20vw;
        z-index: 10; }
    &__cube-middle {
        position: absolute;
        right: -15vw;
        top: -100px;
        z-index: 10;
        @include md {
            right: 0; } }
    &__cube-bottom {
        position: absolute;
        left: -572px;
        top: 0;
        z-index: 100; } }


