.hero {
    position: relative;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: 150;
    &__text {
        position: relative;
        margin-left: 40%;
        text-align: center;
        width: 40%;
        color: #ffffff;
        font-weight: 600;
        z-index: 10;
        @include md {
            width: 50vw;
            margin-left: 40vw; }
        @include sm {
            width: auto;
            margin-left: 0; } }
    &__title {
        font-size: 72px;
        line-height: 1.17; }
    &__descr {
        margin-bottom: 30px;
        font-size: 30px;
        line-height: 1.6;
        @include md {
            font-size: 18px; } }

    &__tv-wrapper {
        position: relative;
        margin-top: 209px;
        @include sm {
            margin-top: 330px; } }
    &__cube-light-shaded {
        position: absolute;
        right: 170px;
        top: -257px; } }

.tv {
    position: relative;
    max-width: 1170px;
    height: 720px;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
    border-radius: 16px;
    &__inner {
        position: absolute;
        top: 48px;
        left: 48px;
        bottom: 48px;
        right: 48px;
        background-color: #000;
        border-radius: 16px; }
    &__play {
        width: 120px;
        height: 120px;
        @include vcenter;
        cursor: pointer;
        svg {
            height: 100%;
            width: 100%; } }
    &__robot {
        position: absolute;
        width: 1200px;
        top: -759px;
        left: -100px;
        z-index: 5;
        pointer-events: none;
        img {
            max-width: 100%; } } }




$tv-padding: 2vw;

@include md {
    .tv {
        width: 90vw;
        height: 53vw;
        margin: 0 auto;
        position: relative;
        background-color: #fff;
        box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
        border-radius: 16px;
        &__inner {
            position: absolute;
            top: $tv-padding;
            left: $tv-padding;
            bottom: $tv-padding;
            right: $tv-padding;
            background-color: #000;
            border-radius: 16px; }
        &__play {
            width: 120px;
            height: 120px;
            @include vcenter;
            svg {
                height: 100%;
                width: 100%; } }
        &__robot {
            top: -55vw;
            left: -10%;
            width: 80vw;
            img {
                width: 100%; } } } }


@include sm {
    .tv {
        &__robot {
            position: absolute;
            top: -464px;
            left: -53px;
            width: 544px; } } }

